import React , {useState} from 'react'
import './Visiter.css'
import { Link } from 'react-router-dom'
import back from '../../Assets/images/back.png';
import image from '../../Assets/images/Mask Group -1.png'
import {BsPersonPlus} from "react-icons/bs"
import * as yup from "yup";
import { yupResolver } from '@hookform/resolvers/yup';
import { useForm } from "react-hook-form";


const schema = yup
  .object({
    email: yup.string().email().required("Please Enter your Email"),
  })


const Visiter = () => {

    const [email , setEmail] = useState("");
    const [number1 , setNumber1] = useState("");
    const [number2 , setNumber2] = useState("");
    const [file , setFile] = useState();

    const { register, handleSubmit , formState:{errors}} = useForm({resolver: yupResolver(schema)});


    const onSubmit = ()=> {
        console.log(email);
        console.log(number1);
        console.log(number2);
        console.log(file);
    }
    return (
    <div className='visiter'>
        <Link to="/" className='back'>
            <span>Back</span>
            <img src={back} alt=""/>
        </Link>
        <div className='section1'>
            <header>
                <span className='icon'><BsPersonPlus/></span>
                <span className='text'>Visitor</span>
            </header>
            <form onSubmit = {handleSubmit(onSubmit)} encType="multipart/form-data">
            <div className='body'>
                <div>
                <input {...register("email")} onChange={(e)=> setEmail(e.target.value)} className='my-2' type="email" placeholder='E-mail address'/>
                <span style={{color:"red"}} className="error">{errors.email?.message}</span>
                </div>
                <span>or</span>
                <div>
                    <input value={number1} onChange={(e)=> setNumber1(e.target.value)} className='my-2' type="number" placeholder='mobile number'/>
                </div>
                <span>or</span>
                <div>
                    <input  value={number2} onChange={(e)=> setNumber2(e.target.value)} className='my-2' type="number" placeholder='Whatsapp number'/>
                </div>
                <div>
                    <input onChange={(e)=> setFile(e.target.files[0])} className='my-4' type="file" placeholder='Uploade file'/>
                </div>
            </div>
            <div className='footer'>
                <button type="submit" className='mt-3 btn btn-dark'>Register</button>
            </div>
            </form>
        </div>
        <div className='section2'>
            <img src={image} alt=""/>
        </div>
    </div>
  )
}

export default Visiter;
